@import "../../global.scss";

.home {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 4rem;
  min-height: calc(100vh - 6.5rem);

  .image {
    flex: 1 1 42rem;
    text-align: center;

    img {
      height: 50rem;
      width: 50rem;
      border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
    }
  }

  .content {
    flex: 1 1 42rem;

    h3 {
      color: $white;
      font-size: 4rem;
      padding-bottom: 0.5rem;
    }

    span {
      font-size: 2.5rem;
      color: $crimson;
      padding: 0.5rem 0;
     
    }
    .ityped-cursor {
      animation: blink 1s infinite;
    }

    @keyframes blink {
      50% {
        opacity: 1;
      }
      100% {
        opacity: 0;
      }
    }

    p {
      font-size: 1.5rem;
      color: $light-color;
      padding: 1rem 0;
      line-height: 2;
    }
    .btn {
      display: inline-block;
      margin-top: 1rem;
      border-radius: 5rem;
      background: crimson;
      padding: 0.7rem;
      font-size: 1.7rem;
      color: #fff;
      padding-left: 2rem;
    }

    .btn .fa-user {
      height: 4rem;
      width: 4rem;
      line-height: 4rem;
      font-size: 1.7rem;
      text-align: center;
      background: #fff;
      color: crimson;
      border-radius: 50%;
      margin-left: 1rem;
      -webkit-transition: 0.2s linear;
      transition: 0.2s linear;
    }

    .btn:hover .fa-user {
      margin-left: 2rem;
    }
  }
}
